.roadmap{
  padding: 3.25em 0 3.75em;
  background-color: $gray;
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 31.25em) 26.25em;
    gap: 5em;
    align-items: flex-start;
  }
  &__text{
    @include grid100gap(1.25em);
  }
  &__img{
    padding-top: 1.83em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    &._open{
      //grid-template-columns: 100%;

      @media screen and (min-width: 576px){
        .roadmap-item:not(._active){
          display: none;
        }
      }
    }
  }
}

.roadmap-item{
  &:nth-child(2){
    @media screen and (min-width: 577px){
      .roadmap-card{
        &__btn{
          flex-direction: row-reverse;
          img{
            transform: rotate(-180deg);
          }
        }
      }

      .roadmap-detail__btn{
        flex-direction: row-reverse;
        svg{
          transform: rotate(-180deg);
          margin: 0 0 0 1.875em;
        }
      }
    }
    @media screen and (min-width: 993px){
      .roadmap-detail{
        grid-template-columns: calc(100% - 35em) 34.5em;
        &__img{
          right: unset;
          left: 0;
          padding-right: 3em;
          padding-bottom: 5em;
        }
        &__col2{
          order: -1;
          max-height: 100%;
        }
      }
    }
  }
  &._active{
    grid-column: 1/-1;
    .roadmap-card{
      display: none;
    }
    .roadmap-detail{
      display: grid;
    }
  }
  &__top{
    @include grid100gap(1.25em);
  }
  &__desc{
    font-size: 1.375em;
    line-height: 150%;
    letter-spacing: -0.03em;
    a{
      cursor: pointer;
    }
  }
}

.roadmap-card,
.roadmap-detail{
  &__btn{
    font-weight: 600;
    padding: 0 1.875em;
    span{
      font-size: 1.375em;
    }
    svg,
    img{
      width: 2.4375em;
    }
  }
}

.roadmap-card{
  height: 100%;
  padding: 1.75em 1.875em 8.125em;
  border-radius: 1.875em;
  background-color: #fff;
  position: relative;
  &__img{
    margin-top: 2.5em;
    @include imgCover(85.6%);
    border-radius: 1.25em;
    ._view2{
      display: none;
    }
  }
  &__btn{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 5em;
    border-radius: 0 0 1.875em 1.875em;
    justify-content: space-between;
  }
}

.roadmap-detail{
  display: none;
  grid-template-columns: 34.5em calc(100% - 35em);
  gap: .5em;
  padding: 1.875em;
  background-color: #fff;
  position: relative;
  border-radius: 1.875em;
  &__col1{
    padding-bottom: 5em;
  }
  &__col2{
    min-height: 50.3125em;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    width: calc(100% - 35em);
    height: 100%;
    padding: 1.875em;
    img{
      width: 100%;
      max-height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 1.5625em;
    }
    ._view2{
      display: none;
    }
  }
  &__btn{
    position: absolute;
    left: 0;
    bottom: 0;
    height: 5em;
    display: flex;
    align-items: center;
    color: $primary;
    svg{
      margin-right: 1.875em;
    }
    &:hover{
      color: $black;
      path{
        stroke: $black;
      }
    }
  }
  &__items{
    margin-top: 3.125em;
  }
}

.text ul,
.m-list{
  @include grid100gap(.625em);
  li{
    font-size: 1em;
    line-height: 160%;
    letter-spacing: -0.03em;
    padding-left: 1.5em;
    position: relative;
    &:before{
      content: '•';
      padding: 0 .5em;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}


@media screen and (max-width: 1280px){
  .roadmap{
    padding: 3.25em 0 2.31em;
    &__top{
      grid-template-columns: calc(100% - 29.6875em) 24.6875em;
    }
    &__text{
      max-width: 44em;
    }
    &__img{
      padding-top: 1.72em;
    }
  }

  .roadmap-item{
    &:nth-child(2){
      @media screen and (min-width: 993px){
        .roadmap-detail{
          grid-template-columns: calc(100% - 36em) 34.5em;
          &__img{
            padding-right: 2em;
          }
        }
      }
    }
    &__top{
      gap: 1.125em;
    }
    &__desc{
      font-size: 1.25em;
    }
  }

  .roadmap-card,
  .roadmap-detail{
    &__btn{
      height: 4.6875em;
      padding: 0 1.75em;
      span{
        font-size: 1.25em;
      }
      svg,
      img{
        width: 2.3125em;
      }
    }
  }

  .roadmap-card{
    padding: 1.57em 1.75em 7.1875em;
    border-radius: 1.25em;
    &__img{
      margin-top: 2.3125em;
      border-radius: .9375em;
    }
    &__btn{
      border-radius: 0 0 1.25em 1.25em;
    }
  }

  .roadmap-detail{
    grid-template-columns: 34.5em calc(100% - 36em);
    gap: 1.5em;
    padding: 1.7em;
    border-radius: 1.25em;
    &__col1{
      padding-bottom: 5em;
    }
    &__col2{
      min-height: 48.125em;
    }
    &__img{
      width: calc(100% - 36em);
      padding: 1.7em;
      img{
        border-radius: .9375em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .roadmap{
    &__top{
      grid-template-columns: calc(100% - 14.75em) 14.75em;
      gap: 0;
      align-items: flex-end;
    }
    &__text{
      gap: .94em;
      .text22{
        max-width: 34.56em;
      }
    }
    &__img{
      width: calc(100% + 4em);
      margin-left: -4em;
      padding-top: 0;
    }
    &__grid{
      margin-top: 1.875em;
    }
  }

  .roadmap-item{
    &:nth-child(2){
      @media screen and (min-width: 577px){
        .roadmap-card{
          &__btn{
            img{
              transform: rotate(135deg);
            }
          }
        }
        .roadmap-detail__btn{
          right: unset;
          left: 0;
          svg{
            transform: rotate(135deg);
            margin: 0 0 0 1.875em;
          }
        }
        .roadmap-detail__img{
          ._view1{
            display: block;
          }
          ._view2{
            display: none;
          }
        }
      }
    }
    &__top{
      gap: .875em;
    }
    &__desc{
      font-size: 1em;
    }
  }

  .roadmap-card{
    padding: 1.31em 1.31em 5.61em;
    border-radius: .9375em;
    &__top{
      gap: .875em;
    }
    &__img{
      margin-top: 1.75em;
      border-radius: .45em;
      padding-bottom: 61%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__btn{
      height: 3.75em;
      border-radius: 0 0 .9375em .9375em;
      padding: 0 1.34em;
      span{
        font-size: 1em;
      }
      img{
        width: 2em;
        transform: rotate(45deg);
      }
    }
  }

  .roadmap-detail{
    border-radius: .9375em;
    grid-template-columns: 100%;
    gap: 0;
    padding: 1.5em 1.25em 4.125em;
    &__col1{
      padding-bottom: 0;
    }
    &__col2{
      display: none;
    }
    &__img{
      width: 100%;
      height: unset;
      padding: 0;
      position: static;
      margin-top: 1.5em;
      img{
        border-radius: .625em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__btn{
      height: 3.75em;
      padding: 0 1.34em;
      left: unset;
      right: 0;
      span{
        font-size: 1em;
      }
      svg{
        width: 2em;
        transform: rotate(45deg);
      }
    }
    &__items{
      margin-top: 2.5em;
    }
  }
}


@media screen and (max-width: 768px){
  .roadmap{
    padding: 2.42em 0 1.8em;
    &__top{
      grid-template-columns: calc(100% - 16.5em) 15em;
      gap: 1.5em;
      align-items: flex-start;
      .text22{
        max-width: 27.25em;
      }
    }
    &__img{
      width: 100%;
      margin-left: 0;
      padding-top: .39em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5625em));
      gap: 1.25em 1.125em;
      margin-top: 2em;
    }
  }

  .roadmap-item{
    &__top{
      gap: .625em;
    }
  }

  .roadmap-card{
    padding: 1.06em 1.06em 4.37em;
    &__top{
      gap: .625em;
    }
    &__img{
      margin-top: 1.375em;
      border-radius: .625em;
    }
    &__btn{
      height: 2.8125em;
      padding: 0 1.06em;
      img{
        width: 1.75em;
      }
    }
  }

  .roadmap-detail{
    padding: .9375em .9375em 4.75em;
    &__img{
      margin-top: 1.375em;
    }
    &__btn{
      height: 4.75em;
      padding: 0 .9375em;
      svg{
        width: 1.6875em;
      }
    }
    &__items{
      margin-top: 1.875em;
    }
  }

  .text ul,
  .m-list{
    gap: .25em;
  }
}


@media screen and (max-width: 576px){
  .roadmap{
    padding: 2.125em 0 1.625em;
    &__top{
      grid-template-columns: 100%;
      gap: 1.43em;
    }
    &__img{
      padding-top: 0;
      order: -1;
      max-width: 11.6em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .roadmap-card{
    padding-bottom: 4.68em;
    &__btn{
      img{
        width: 1.375em;
        transform: rotate(90deg);
      }
    }
  }

  .roadmap-detail{
    padding: 1em .875em 3.875em;
    &__img{
      margin-top: 1.375em;
    }
    &__btn{
      height: 2.5em;
      padding: 0 .875em;
      justify-content: space-between;
      width: 100%;
      svg{
        width: 1.375em;
        order: 2;
        margin: 0;
        transform: rotate(90deg);
      }
    }
    &__items{
      margin-top: 1.5em;
    }
  }
}
