.course-detail{
  padding: 7.5em 0;
  background-color: $gray;
  &__top{
    @include grid100gap(1em);
    text-align: center;
  }
  &__items{
    margin: 2.1875em auto 0;
    width: 69.6875em;
    max-width: 100%;
  }
}

.click-hint{
  display: inline-flex;
  align-items: center;
  margin: .25em auto 0;
  padding: .2em .875em;
  border-radius: 0.625em;
  background-color: #fff;
  min-height: 3.125em;
  text-align: left;
  img{
    width: 2.8125em;
    flex-shrink: 0;
    margin-right: .4em;
  }
}

.course-card{
  &._active{
    .course-card__show{
      border-color: $primary;
    }
    .course-card__btn{
      background-color: transparent;
      &:hover{
        background-color: transparent;
      }
      ._hide{
        opacity: 0;
      }
      path{
        stroke: $primary;
      }
    }
  }
  &__show{
    transition: all .3s;
    display: grid;
    grid-template-columns: 6.5em calc(100% - 14em) 3.75em;
    align-items: center;
    gap: 1.875em;
    padding: 1.56em 0;
    border-bottom: 1px solid #fff;
  }
  &__img{
    border-radius: .625em;
    @include imgCover(76.2%);
  }
  &__btn{
    width: 3.75em;
    height: 3.75em;
    background-color: $primary;
    border-radius: 50%;
    border: 2px solid $primary;
    @include center;
    svg{
      width: 1.3125em;
    }
    &:hover{
      background-color: darken($primary,10%);
      border-color: darken($primary,10%);
    }
  }
  &__hidden{
    display: none;
    margin-top: 1.56em;
    .course-card__img{
      padding: 0;
      img{
        position: static;
        height: auto;
      }
    }
  }
  .text ul{
    @include lastM(0 0 1em);
    li{
      padding-left: 1em;
      &:before{
        color: $primary;
        padding-left: 0;
      }
    }
  }
  .text{
    margin-top: 1.25em;
    max-width: 64em;
    p{
      font-size: 1em;
      line-height: 160%;
      letter-spacing: -.03em;
    }
  }
}



@media screen and (max-width: 1280px){
  .click-hint{
    margin-top: .875em;
  }

  .course-card{
    .text{
      p{
        line-height: 150%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .course-detail{
    &__top{
      gap: 1.75em;
    }
  }

  .click-hint{
    margin-top: .125em;
  }

  .course-card{
    &__show{
      grid-template-columns: 5.9375em calc(100% - 3.125em - 5.9375em - 3.75em) 3.125em;
    }
    &__btn{
      width: 3.125em;
      height: 3.125em;
      svg{
        width: 1.125em;
      }
    }
    &__hidden{
      margin-top: 1.56em;
    }
  }
}


@media screen and (max-width: 768px){
  .course-detail{
    padding: 6.25em 0;
    &__top{
      gap: .9375em;
    }
  }

  .click-hint{
    margin-top: .5em;
  }

  .course-card{
    &__hidden{
      margin-top: 1.25em;
    }
    .m-list{
      margin-top: .94em;
    }
  }
}


@media screen and (max-width: 576px){
  .click-hint{
    max-width: 18em;
    padding-left: .81em;
    padding-right: .81em;
  }

  .course-card{
    &__show{
      grid-template-columns: 4.375em calc(100% - 1.875em - 4.375em - 2.8125em) 2.8125em;
      gap: .9375em;
    }
    &__btn{
      width: 2.8125em;
      height: 2.8125em;
    }
  }
}
