.experience{
  padding: 3.75em 0 17.5em;
  background-color: $gray;
  position: relative;
  overflow: hidden;
  .container{
    position: relative;
    z-index: 2;
  }
  &__anchor{
    margin: 0 auto 2.1875em;
    display: block;
    width: 6.25em;
  }
  &__text{
    margin: 0 auto;
    text-align: center;
    width: 66.875em;
    max-width: 100%;
    @include grid100gap(1.56em);
  }
  &__grid{
    margin-top: 3.75em;
    display: flex;
    justify-content: space-between;
  }
  &__col,
  &__col2{
    position: relative;
  }
  &__col{
    .experience__info{
      top: 21.635em;
      left: 100%;
      margin-left: 4.69em;
      white-space: nowrap;
      img{
        width: 6.5em;
        top: 100%;
        left: -.625em;
        margin-top: .75em;
      }
    }
  }
  &__col2{
    padding-top: 9.31em;
    .experience__info{
      top: 3.96em;
      left: -16em;
      img{
        left: 9em;
        top: 100%;
        margin-top: .75em;
        width: 5.125em;
      }
    }
  }
  &__info{
    position: absolute;
    .h2{
      text-transform: unset;
      font-weight: 400;
    }
    img{
      position: absolute;
    }
    ._view2{
      display: none;
    }
  }
  &__bg{
    position: absolute;
    width: 41.5em;
  }
  &__bg1{
    left: 0;
    bottom: 20.5em;
  }
  &__bg2{
    right: 0;
    bottom: 10em;
  }
}

.gallery,
.gallery2{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .3125em));
  gap: .625em;
  .gallery-item{
    &:nth-child(1){
      grid-column: 1/-1;
    }
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5){
      height: 13.9em;
    }
    &:nth-child(2){
      .gallery-item__img{
        height: 117%;
      }
    }
    &:nth-child(4){
      .gallery-item__img{
        height: 83%;
        margin-top: auto;
      }
    }
    &:nth-child(3){
      .gallery-item__img{
        height: 58%;
      }
    }
    &:nth-child(5){
      .gallery-item__img{
        height: 142%;
        margin-top: calc(-42% - .625em);
      }
    }
  }
}

.gallery{
  width: 25em;
  .gallery-item{
    &:nth-child(1){
      max-height: 16.875em;
      .gallery-item__img{
        width: 127%;
      }
    }
  }
}

.gallery2{
  width: 25.3125em;
  .gallery-item{
    &:nth-child(1){
      max-height: 17.5em;
    }
    &:nth-child(4){
      .gallery-item__img{
        width: 156%;
        margin-left: -56%;
      }
    }
  }
}

.gallery-item{
  position: relative;
  display: flex;
  flex-direction: column;
  &__img{
    flex-shrink: 0;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: .625em;
    position: relative;
    &:hover{
      &:before{
        opacity: 1;
      }
    }
    img{
      border-radius: inherit;
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
    &:before{
      content: '';
      position: absolute;
      border-radius: inherit;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/hand3.svg), linear-gradient(0deg, rgba(0, 0, 0, 0.40) 0%, rgba(0, 0, 0, 0.40) 100%);
      background-size: 5.625em auto, cover;
      background-repeat: no-repeat;
      background-position: center;
      transition: all .3s;
      opacity: 0;
    }
  }
}


@media screen and (max-width: 1280px){
  .experience{
    padding: 2.375em 0 16.875em;
    &__anchor{
      margin-bottom: 1.875em;
      width: 6em;
    }
    &__text{
      width: 61.95em;
      gap: 1.25em;
    }
    &__col2{
      .experience__info{
        left: -15em;
        img{
          left: 7em;
          width: 4.875em;
        }
      }
    }
    &__bg{
      width: 35em;
    }
    &__bg1{
      left: -12em;
    }
    &__bg2{
      right: -14em;
    }
  }

  .gallery,
  .gallery2{
    font-size: .96em;
  }

  .gallery{
    .gallery-item{
      &:nth-child(1){
        max-height: 16.25em;
      }
    }
  }

  .gallery2{
    .gallery-item {
      &:nth-child(1) {
        max-height: 16.5625em;
      }
    }
  }
}


