    }
    &__bg2{
      right: -17em;
    }
  }

  .gallery,
  .gallery2{
    font-size: .735em;
  }

  .gallery{
    .gallery-item{
      &:nth-child(1){
        max-height: 12.5em;
      }
    }
  }

  .gallery2{
    .gallery-item {
      &:nth-child(1) {
