@media screen and (max-width: 768px){
  .experience{
    padding: 1.875em 0 11.875em;
    &__anchor{
      margin-bottom: 1em;
      width: 3.5em;
    }
    &__text{
      gap: .625em;
      .text36{
        max-width: 27em;
        margin: 0 auto;
      }
    }
    &__grid{
      margin-top: 3.125em;
    }
    &__col{
      .experience__info{
        top: 12.52em;
        margin-left: 2.75em;
        img{
          width: 3.8125em;
        }
      }
    }
    &__col2{
      padding-top: 6.29em;
      .experience__info{
        left: -9em;
        top: 3.19em;
        img{
          left: 4em;
          width: 3em;
        }
      }
    }
    &__bg{
      font-size: .875em;
    }
    &__bg1{
      bottom: 15em;
    }
    &__bg2{
      right: -15em;
      font-size: .75em;
    }
  }

  .gallery,
  .gallery2{
    font-size: .579em;
  }

  .gallery{
    .gallery-item{
      &:nth-child(1){
        max-height: 9.6875em;
      }
    }
  }

  .gallery2{
    .gallery-item {
      &:nth-child(1) {
        max-height: 10em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .experience{
    padding: 1.5625em 0 11.25em;
    &__text{
      br{
        display: none;
      }
    }
    &__grid{
      margin-top: 3.21em;
      @include grid100gap(6.25em);
    }
    &__info{
      display: inline-block;
      position: relative;
      left: unset !important;
      top: unset !important;
      margin: 0 0 1.98em !important;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      img{
        left: 100% !important;
        right: unset !important;
        top: .5em !important;
        margin-left: .44em !important;
        width: 4em !important;
      }
    }
    &__col2{
      padding: 0;
    }
    &__bg1{
      bottom: unset;
      top: 46em;
      left: -15em;
    }
    &__bg2{
      right: -12em;
    }
  }

  .gallery,
  .gallery2{
    width: 100%;
    font-size: 1em;
    .gallery-item{
      &:nth-child(1){
        width: 100%;
        .gallery-item__img{
          width: 100%;
        }
      }
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5){
        height: 12.03125em;
      }
      &:nth-child(5){
        .gallery-item__img{
          margin-top: calc(-42% - .625em);
        }
      }
    }
  }

  .gallery{
    .gallery-item{
      &:nth-child(1){
        max-height: 11.25em;
      }
    }
  }

  .gallery2{
    .gallery-item{
      &:nth-child(1) {
        max-height: 14.6875em;
      }
      &:nth-child(4){
        .gallery-item__img{
          width: 100%;
          margin-left: 0;
        }
      }
    }
  }

  .gallery-item{
    &__img{
      &:before{
        background-size: 4.4375em auto,cover;
      }
    }
  }
}
