.course2{
  padding: 6.875em 0;
  &__inner{
    width: 100%;
    margin: 0 auto;
    max-width: 73.75em;
  }
  &__title{
    text-align: center;
    text-transform: uppercase;
    font-weight: 600;
  }
  &__lamp{
    margin: 2.19em auto 0;
    display: block;
    width: 6.25em;
  }
  &__desc{
    margin-top: .625em;
    text-align: center;
    b{
      font-weight: 600;
    }
  }
}

.example{
  padding: 2.82em 0 0;
  border-radius: 1.25em;
  border: 2px solid $black;
  margin-top: 3.5em;
  position: relative;
  &__text{
    width: calc(100% + 4px);
    margin-left: -2px;
    margin-bottom: -2px;
    background-color: $black;
    color: #fff;
    padding: 1.375em 7.5em;
    text-align: center;
    border-radius: 1.25em;
  }
  &__title{
    padding: 0 2.25em;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%,-50%);
    font-weight: 600;
    color: $primary;
    text-transform: uppercase;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 4.2em));
    gap: 2.5em 8.4em;
    padding: 0 3.19em 2.5em;
  }
}

.example-group{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    margin-top: 1em;
  }
  &__img{
    @include imgCover(63.6%);
    border-radius: .9375em;
    margin-top: .75em;
  }
  &__title{
    font-family: $font2;
    display: inline-block;
    color: $primary;
    position: relative;
    span{
      font-size: 1.375em;
      line-height: 80%
    }
    img{
      position: absolute;
      left: 100%;
      margin-left: .25em;
      bottom: -.25em;
      width: 2.375em;
    }
  }
}

.ready{
  margin-top: 6.25em;
  &__text{
    @include grid100gap(.94em);
    margin-top: 1.875em;
    text-align: center;
  }
  &__desc{
    max-width: 53.625em;
    margin: 0 auto;
  }
  &__icon{
    display: block;
    margin: 0 auto;
    width: 6.625em;
  }
}


@media screen and (max-width: 1280px){
  .course2{
    padding: 7em 0;
    &__lamp{
      margin-top: 2.56em;
      width: 5.9375em;
    }
    &__desc{
      margin-top: .94em;
    }
  }

  .example{
    padding-top: 2.19em;
    &__text{
      padding: 1.56em 10em;
    }
    &__title {
      padding: 0 2.75em;
    }
    &__grid{
      padding: 0 2.5em 2.5em;
    }
  }

  .ready{
    &__text{
      margin-top: 1.875em;
    }
    &__icon{
      width: 6.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .course2{
    &__lamp{
      margin-top: 3.125em;
      width: 4.6875em;
    }
    &__desc{
      margin-top: 1.56em;
    }
  }

  .example{
    padding-top: 1.56em;
    &__text{
      padding: 1.25em 2.5em;
    }
    &__title {
      padding: 0 1.625em;
    }
    &__grid{
      padding: 0 2em 1.875em;
      grid-template-columns: repeat(2,calc(50% - 1.75em));
      gap: 2.5em 3.5em;
    }
  }

  .example-group{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.125em;
    }
    &__title{
      span{
        font-size: 1.25em;
      }
      img{
        bottom: -1.25em;
      }
    }
    &__img{
      margin-top: 2.06em;
    }
  }

  .ready{
    margin-top: 3.75em;
    &__icon{
      width: 5.625em;
    }
  }
}


@media screen and (max-width: 768px){
  .course2{
    padding: 5.94em 0;
    &__lamp{
      margin-top: 2.5em;
      width: 3.4375em;
    }
    &__desc{
      margin-top: 1.25em;
    }
  }

  .example{
    &__title {
      padding: 0 1.44em;
    }
    &__grid{
      padding: 0 1.37em 2.5em;
      grid-template-columns: 100%;
    }
  }

  .ready{
    &__text{
      margin-top: 1.25em;
    }
    &__icon{
      width: 3.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .course2{
    &__lamp{
      margin-top: 1.875em;
    }
    &__desc{
      margin-top: .94em;
    }
  }

  .example{
    border-radius: .9375em;
    &__title {
      padding: 0 1.25em;
    }
    &__text{
      border-radius: .9375em;
      padding: 1.25em 1em;
    }
    &__grid{
      gap: 3.125em;
      padding: 0 1.25em 3.125em;
    }
  }

  .example-group{
    &__items{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .ready{
    &__text{
      margin-top: .94em;
      gap: .625em;
    }
    .text36{
      font-size: 1em;
    }
  }
}
