.chat{
  margin-top: 3.75em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.chat-item{
  display: flex;
  align-items: flex-start;
  margin-left: auto;
  &__content{
    margin-top: 3.52em;
    margin-right: 2.75em;
    max-width: 27.1875em;
    padding: 1em 1.81em 1em 2em;
    border-radius: 0.625em;
    background: #474747;
    color: #FFF;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      background-image: url(../img/triangle.svg);
      @include bgImgSize(100%);
      right: -2.06em;
      top: -.38em;
      width: 3.75em;
      height: 4.04344em;
    }
    .text16{
      position: relative;
      z-index: 2;
    }
  }
  &__img{
    width: 5.9375em;
    height: 5.9375em;
    padding: .27em;
    flex-shrink: 0;
    border-radius: 50%;
    border: 2px dashed $primary;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__img2{
    order: -1;
    width: 6.3125em;
    flex-shrink: 0;
    margin-right: 2.85em;
    img{
      width: 100%;
      display: block;
    }
  }
  &._admin {
    & + ._user {
      margin-top: -1.6em;
    }
  }
  &._user{
    margin-left: 0;
    &+._admin{
      margin-top: -1.6em;
    }
    .chat-item__content{
      max-width: 25em;
      margin-right: 0;
      padding-left: 1.19em;
      &:before{
        top: -.29em;
        right: unset;
        left: -2em;
        background-image: url(../img/triangle2.svg);
      }
    }
  }
}


@media screen and (max-width: 1280px){

}


@media screen and (max-width: 992px){
  .chat{
    margin-top: 3.125em;
  }
}


@media screen and (max-width: 768px){

}


@media screen and (max-width: 576px){
  .chat-item{
    &._admin {
      & + ._user {
        margin-top: 1.25em;
      }
    }
    &._user{
      & + ._admin{
        margin-top: 1.25em;
      }
      .chat-item__content{
        max-width: 23em;
        &:before{
          top: 0;
          left: -1.37em;
        }
      }
    }
    &__img{
      width: 3.4375em;
      height: 3.4375em;
      padding: .25em;
    }
    &__content{
      padding: .4em .81em;
      margin-top: .625em;
      margin-right: 2em;
      &:before{
        width: 2.5625em;
        height: 2.5625em;
        top: 0;
        right: -1.37em;
      }
      .text16{
        position: relative;
        z-index: 2;
      }
    }
    &__img2{
      width: 3.4375em;
      margin-right: 2em;
    }
  }
}
