.lang{
  padding: 7.5em 0;
  &__inner{
    position: relative;
  }
  &__img{
    width: 100%;
    display: block;
    &._view2,
    &._view3{
      display: none;
    }
  }
  &__top{
    max-width: 40.16em;
  }
  &__text{
    @include grid100gap(2.25em);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 18.59em;
    width: 60.3125em;
    max-width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}


@media screen and (max-width: 1280px){
  .lang{
    &__top{
      max-width: 33.6em;
    }
    &__text{
      top: 16.87em;
      width: 54.6em;
    }
  }
}


@media screen and (max-width: 992px){
  .lang{
    &__top{
      max-width: 29em;
    }
    &__text{
      top: 12.93em;
      width: 44.3125em;
      gap: 1.5em;
    }
  }
}


@media screen and (max-width: 768px){
  .lang{
    padding: 6.25em 0;
    &__img{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__top{
      max-width: 22.3125em;
    }
    &__text{
      top: 10.13em;
      width: 34.4375em;
    }
  }
}


@media screen and (max-width: 576px){
  .lang{
    &__img{
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
      &._view2{
        display: none;
      }
      &._view3{
        display: block;
      }
    }
    &__top{
      max-width: 20.0625em;
    }
    &__text{
      top: 7.44em;
      width: 100%;
      gap: .5em;
    }
  }
}
