.book{
  padding: 7.5em 0;
  &__top{
    text-align: center;
    width: 70.875em;
    max-width: 100%;
    margin: 0 auto;
    .h2{
      padding-top: .1em;
      padding-bottom: .1em;
    }
  }
  &__subtitle{
    margin: 1.75em auto .94em;
    max-width: 55.5625em;
  }
  &__bottom{
    margin-top: 3.75em;
    position: relative;
  }
  &__img{
    border-radius: 1.25em;
    img{
      width: 100%;
      display: block;
      border-radius: inherit;
    }
  }
  &__text{
    position: absolute;
    top: 1.875em;
    left: 1.875em;
    width: 44.0625em;
    padding: 1.12em 1.69em;
    background-color: #fff;
    border-radius: 0.9375em;
    border: 2px solid $primary;
  }
  &__hint{
    position: absolute;
    bottom: 1.875em;
    left: 1.875em;
    height: 6.0625em;
    padding: 0 2.625em;
    border-radius: 0.9375em;
    background-color: $primary;
    color: #fff;
    display: flex;
    align-items: center;
    font-weight: 600;
    img{
      width: 4.4375em;
      flex-shrink: 0;
      margin-right: .94em;
    }
  }
}


@media screen and (max-width: 1280px){
  .book{
    &__subtitle{
      margin: 2.06em auto .94em;
      max-width: 51.25em;
    }

  }
}


@media screen and (max-width: 992px){
  .book{
    padding: 8.19em 0 4.625em;
    &__subtitle{
      margin: 1.7em auto .94em;
      max-width: 43.5625em;
    }
    &__bottom{
      margin-top: 1.69em;
    }
    &__img{
      border-radius: .9375em;
    }
    &__text{
      width: 35.625em;
    }
    &__hint{
      height: 5.4375em;
    }
  }
}


@media screen and (max-width: 768px){
  .book{
    padding: 6.56em 0 6.25em;
    &__top{
      .text22{
        max-width: 35em;
        margin: 0 auto;
      }
    }
    &__subtitle{
      margin: 1.87em auto .625em;
      max-width: 41.375em;
    }
    &__bottom{
      margin-top: 2.5em;
    }
    &__text{
      width: 25.625em;
      top: 1.25em;
      left: 1.25em;
      padding: .75em 1em;
    }
    &__hint{
      bottom: 1.25em;
      left: 1.25em;
      padding: 0 2.875em 0 1.875em;
      border-radius: 0.625em;
      img{
        width: 3.5em;
        margin-right: .625em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .book{
    &__top{
      padding: 0 .25em;
    }
    &__subtitle{
      margin-top: 1.56em;
    }
    &__text{
      width: 100%;
      position: static;
      margin-top: .625em;
      padding: .625em .5em .625em .625em;
    }
    &__hint{
      position: static;
      margin-top: .625em;
      width: 100%;
      padding: 0 2.06em;
      height: 3.4375em;
      img{
        width: 3em;
      }
    }
  }
}
