.reviews{
  padding: 7.5em 0;
  &__grid{
    margin-top: 4.25em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.6875em * .75));
    gap: 3.125em 1.6875em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3.125em;
  }
  &__btn{
    padding: 0 1.75em;
    border-radius: 0.625em;
    height: 4.6875em;
    font-weight: 600;
    span{
      font-size: 1.375em;
      line-height: 150%;
      letter-spacing: -.03em;
    }
    svg{
      width: 1.8125em;
      margin-left: 1.25em;
    }
    &._hide{
      display: none;
    }
  }
}

.review-card{
  display: none;
  @media screen and (min-width: 993px){
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6),
    &:nth-child(7),
    &:nth-child(8){
      display: block;
    }
  }
  &._active{
    display: block;
  }
  &__img{
    @include imgCover(176.7%);
    border-radius: .9375em;
    img{
      border: 1px solid $black;
    }
  }
  &__user{
    display: flex;
    align-items: center;
    margin-top: 1.375em;
    img{
      margin-right: .625em;
      width: 1.375em;
      flex-shrink: 0;
    }
  }
}


@media screen and (max-width: 1280px){
  .reviews{
    &__btn{
      padding: 0 2.375em;
      height: 4.375em;
      span{
        font-size: 1.25em;
      }
    }
  }

  .review-card{
    &__user{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .reviews{
    &__grid{
      grid-template-columns: repeat(2,calc(100% / 3 - 1.6875em * 2 / 3));
      gap: 2.5em 1.6875em;
    }
    &__nav{
      margin-top: 2.5em;
    }
    &__btn{
      padding: 0 2.75em;
      span{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .reviews{
    padding: 6.25em 0;
    &__grid{
      margin-top: 3.44em;
    }
  }
}


@media screen and (max-width: 576px){
  .reviews{
    &__grid{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
