.community{
  padding: 8em 0 7.5em;
  background-color: $gray;
  &__top{
    @include grid100gap(2.25em);
    margin: 0 auto;
    text-align: center;
    width: 73.25em;
    max-width: 100%;
  }
  &__bottom{
    margin-top: 3.75em;
    position: relative;
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: 1.25em;
  }
  &__text{
    position: absolute;
    top: 1.875em;
    left: 50%;
    transform: translateX(-50%);
    width: 65.625em;
    max-width: 100%;
    padding: .81em 1.875em;
    border-radius: 0.9375em;
    background-color: #fff;
    border: 2px solid $primary;
    text-align: center;
  }
}


@media screen and (max-width: 1280px){
  .community{
    &__top{
      width: 67.75em;
    }
    &__text{
      padding: .8em 2.3em;
      width: 62.1875em;
    }
  }
}


@media screen and (max-width: 992px){
  .community{
    padding-top: 7.5em;
    &__top{
      gap: 1.56em;
      width: 48.5em;
    }
    &__text{
      padding: 1.4em;
      width: 54.3125em;
    }
  }
}


@media screen and (max-width: 768px){
  .community{
    padding: 6.25em 0;
    &__top{
      width: 41.125em;
    }
    &__bottom{
      margin-top: 3.125em;
    }
    &__text{
      padding: .94em 2.5em;
      width: 43.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .community{
    &__bottom{
      margin-top: 2.5em;
    }
    &__img{
      border-radius: .9375em;
    }
    &__text{
      position: static;
      margin-top: .625em;
      padding: .72em .94em;
      width: 100%;
      transform: unset;
    }
  }
}
