.course{
  padding: 7.58em 0 2.875em;
  &__text{
    max-width: 53.25em;
    width: 100%;
    margin: 7.5em auto 0;
    text-align: center;
  }
}

.course-info{
  margin-top: 3.75em;
  border-radius: 1.25em;
  position: relative;
  img{
    width: 100%;
    display: block;
    border-radius: inherit;
  }
  &__text{
    position: absolute;
    padding: 1.25em;
    background-color: #fff;
    border-radius: 0.9375em;
    max-width: 36.5625em;
    font-weight: 600;
    left: 1.875em;
    bottom: 1.875em;
  }
}

.course-advantages{
  margin-top: 7.83em;
  position: relative;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.advantage-card{
  height: 22.1875em;
  @include bgImgSize(100%);
  position: relative;
  padding: 11.44em 1.5em 2.75em;
  &:nth-child(1){
    background-image: url(../img/advantage-bg.svg);
  }
  &:nth-child(2){
    background-image: url(../img/advantage-bg5.svg);
    .advantage-card__img{
      left: unset;
      right: 0;
      top: -5.64em;
      width: 24.5em;
    }
    .advantage-card__desc{
      max-width: 31.875em;
    }
  }
  &__img{
    position: absolute;
    top: -8.5em;
    left: 4.75em;
    width: 23.6875em;
  }
  &__text{
    @include grid100gap(.625em);
    position: relative;
    z-index: 2;
  }
  &__title{
    font-weight: 600;
    text-transform: uppercase;
  }
}

.offline-mode{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: -4.125em;
  width: 25.3125em;
  padding: .625em 1em;
  background-color: $primary;
  color: #fff;
  border-radius: 1.25em;
  text-align: center;
  img{
    width: 3.1875em;
    display: block;
    margin: 0 auto .44em;
  }
  b{
    font-weight: 600;
  }
}

.what-learn{
  margin-top: 8em;
  &__inner{
    position: relative;
  }
  &__items{
    column-count: 2;
    column-gap: 3.125em;
    margin-top: 4em;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 36.25em;
  }
}

.learn-item{
  display: inline-grid;
  align-items: flex-start;
  grid-template-columns: 5.625em calc(100% - 5.625em - 1.5625em);
  justify-content: space-between;
  width: 100%;
  margin-bottom: 3.125em;
  &:last-child{
    margin-bottom: 5em;
  }
  &__content{
    @include grid100gap(.31em);
    max-width: 28.75em;
  }
  &__title{
    font-weight: 700;
    text-transform: uppercase;
    max-width: 19em;
  }
  &__img{
    @include imgCover(100%);
  }
  &__desc{
    *{
      line-height: inherit;
    }
    p{
      @include lastM(0 0 .5em);
    }
  }
}

.custom-title{
  .h2{
    box-decoration-break: clone;
    background-color: rgba($primary,.3);
    display: inline;
    position: relative;
    line-height: 135%;
    padding: 0 .125em;
    &::before,
    &::after {
      content: "";
      display: inline-block;
      width: .148em;
      height: 1.53em;
      position: absolute;
      background-image: url(../img/line.svg);
      @include bgImgSize(100%);
    }
    &::before{
      transform: translate(-.19em,-.11em);
    }
    &::after{
      transform: translate(.03em,-.11em);
    }
  }
  &._white,
  &._black{
    .h2{
      padding: .15em .1875em;
      background-color: $black;
      color: #fff;
      &::before,
      &::after {
        background-image: url(../img/line2.svg);
        height: 1.92em;
      }
      &::before{
        transform: translate(-.25em,-.29em);
      }
      &::after{
        transform: translate(.1em,-.29em);
      }
    }
  }
  &._white{
    .h2{
      background-color: #fff;
      color: $primary;
      &::before,
      &::after {
        background-image: url(../img/line3.svg);
      }
    }
  }
  &._small{
    .h2{
      font-size: 2.25em;
    }
  }
}

._p-inline-desktop{
  line-height: inherit;
  @media screen and (min-width: 577px){
    display: inline;
  }
}


@media screen and (max-width: 1280px){
  .course{
    padding: 7.5em 0 7.06em;
  }

  .course-info{
    margin-top: 5.61em;
    &__text{
      padding: 1.56em 1.19em;
    }
  }

  .course-advantages{
    margin-top: 8em;
  }

  .advantage-card{
    height: 19.375em;
    padding: 10em 1.625em 1.25em;
    &:nth-child(1){
      background-image: url(../img/advantage-bg2.svg);
    }
    &:nth-child(2){
      background-image: url(../img/advantage-bg6.svg);
      .advantage-card__img{
        top: -5.25em;
        width: 22.3125em;
      }
    }
    &__img{
      top: -8em;
      left: 4.45em;
      width: 22.19em;
    }
    &__text{
      max-width: 30em;
    }
    &__title{
      @media screen and (min-width: 993px){
        br{
          display: none;
        }
      }
    }
  }

  .offline-mode{
    top: -3.5em;
    width: 23.75em;
    padding: .625em;
    img{
      width: 2.75em;
      margin-bottom: .31em;
    }
  }

  .what-learn{
    margin-top: 7.5em;
    &__items{
      column-gap: 2.875em;
    }
    &__img{
      bottom: -3em;
      width: 30.875em;
    }
  }

  .custom-title{
    &._small{
      .h2{
        font-size: 2.0625em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .course{
    &__text{
      max-width: 50.75em;
    }
  }

  .course-info{
    margin-top: 4.06em;
    border-radius: .9375em;
    &__text{
      padding: 1em;
      border-radius: 0.625em;
      max-width: 28.125em;
    }
  }

  .course-advantages{
    margin-top: 4.75em;
  }

  .advantage-card{
    height: 18.9375em;
    padding: 8.75em 1.35em 1.19em;
    &:nth-child(1){
      background-image: url(../img/advantage-bg3.svg);
    }
    &:nth-child(2){
      background-image: url(../img/advantage-bg7.svg);
      .advantage-card__img{
        right: -.29em;
        top: -2.21em;
        width: 17.5625em;
      }
    }
    &__img{
      top: -4.27em;
      left: 3.45em;
      width: 16.875em;
    }
    &__text{
      gap: .44em;
    }
  }

  .offline-mode{
    border-radius: 0.9375em;
    top: -2.75em;
    width: 18.125em;
    padding: .5em .625em;
    img{
      width: 2.125em;
      margin-bottom: .25em;
    }
  }

  .what-learn{
    &__inner{
      padding-bottom: 10em;
    }
    &__items{
      margin-top: 3.81em;
      column-count: 1;
    }
    &__img{
      bottom: 0;
      width: 22.8125em;
    }
  }

  .learn-item{
    margin-bottom: 2.5em;
    &:last-child{
      margin-bottom: 0;
      max-width: 45em;
    }
    &__content,
    &__title{
      max-width: unset;
    }
    &__title{
      font-size: 1.75em;
    }
  }

  .custom-title{
    &._small{
      .h2{
        font-size: 1.75em;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .course{
    padding: 6.5em 0 5.875em;
    &__text{
      margin-top: 6.25em;
      max-width: 41.25em;
    }
  }

  .course-info{
    margin-top: 2.68em;
    &__text{
      bottom: 1.25em;
      left: 1.25em;
    }
  }

  .course-advantages{
    margin-top: 4em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5625em));
      gap: 2.81em 1.125em;
    }
  }


  .advantage-card{
    height: 19.375em;
    &:nth-child(1){
      background-image: url(../img/advantage-bg4.svg);
    }
    &:nth-child(2){
      background-image: url(../img/advantage-bg8.svg);
      .advantage-card__img{
        right: 0;
        top: -.625em;
        width: 11.5em;
      }
    }
    &__img{
      top: -3em;
      left: 2em;
      width: 11.3125em;
    }
  }

  .offline-mode{
    border-radius: .73em;
    top: -2.25em;
    width: 16.1875em;
    padding: .32em 1em .75em;
    img{
      width: 2em;
    }
  }

  .what-learn{
    margin-top: 6.25em;
    &__items{
      margin-top: 3.25em;
    }
    &__inner{
      padding-bottom: 8em;
    }
    &__img{
      width: 19.5625em;
    }
  }

  .learn-item{
    &:last-child{
      max-width: 39em;
      .learn-item__desc{
        max-width: 25em;
      }
    }
    &__title{
      font-size: 1.375em;
    }
  }

  .custom-title{
    &._small{
      .h2{
        font-size: 1.375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .course{
    padding-bottom: 4em;
    &__text{
      max-width: 19.375em;
    }
  }

  .course-info{
    margin-top: 2em;
    border-radius: .625em;
    &__text{
      position: static;
      margin-top: .5em;
      padding: .625em 2.125em .625em .875em;
      background-color: $gray;
    }
  }

  .course-advantages{
    margin-top: 2.34em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .advantage-card{
    background-image: unset !important;
    border-radius: 0.9375em;
    border: 2px solid $primary;
    height: unset;
    padding: 8.125em 1em 1em;
    &:nth-child(2){
      .advantage-card__img{
        right: 2em;
        top: -1.19em;
        width: 14.8125em;
      }
    }
    &__img{
      top: -3.44em;
      left: 5.5em;
      width: 13.625em;
    }
  }

  .offline-mode{
    position: static;
    width: 100%;
    padding: .5em 1.75em;
    transform: unset;
    margin-bottom: 4.625em;
    img{
      margin-bottom: .19em;
    }
  }

  .what-learn{
    &__inner{
      padding-bottom: 7.625em;
    }
    &__img{
      width: 12em;
      bottom: -1em;
    }
  }

  .learn-item{
    position: relative;
    display: block;
    margin-bottom: 2.81em;
    &__top{
      padding-left: 6.875em;
      display: flex;
      align-items: center;
      height: 5.625em;
      margin-bottom: .94em;
    }
    &__img{
      position: absolute;
      top: 0;
      left: 0;
      width: 5.625em;
      height: 5.625em;
      padding: 0;
    }
    &__title{
      font-size: 1em;
    }
  }

  .custom-title{
    &._small{
      .h2{
        font-size: 1.625em;
      }
    }
  }

  ._p-inline-desktop{
    @include lastM(0 0 .75em);
  }
}
