.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(51, 51, 51, 0.70);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
}

.modal {
    overflow-y: auto;
    padding: 2em 0;
    text-align: center;
    @media screen and (min-width: 993px) {

    }

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }

    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &__dialog {
        background: $gray;
        width: 80em;
        max-width: calc(100% - 5em);
        position: relative;
        z-index: 3;
        padding: 4em 5em 5em;
        display: inline-block;
        border-radius: 1.25em;
    }

    &__close {
        background-color: transparent;
        @include center;
        color: $primary;
        font-weight: 600;
        &:hover {
            path {
                opacity: 1;
            }
        }
        svg {
            width: 2.4375em;
            height: auto;
            margin-right: 1.875em;
        }
        span{
            font-size: 1.375em;
            line-height: 150%;
            letter-spacing: -.03em;
        }
        &:hover {
            color: $black;
            path {
                stroke: $black;
            }
        }
    }
    &__info{
        @include grid100gap(.94em);
        max-width: 51em;
        margin-top: 3.75em;
    }
}

.book-modal{
    &__items{
        margin-top: 2.5em;
        .course-card{
            &__show{
                display: flex;
                gap: unset;
                justify-content: space-between;
            }
            &__btn{
                margin-left: 1em;
                flex-shrink: 0;
            }
            .text{
                ul li:before{
                    color: $black;
                }
            }
        }
    }
}

.modal2{
    .modal__dialog{
        border-radius: 0.9375em;
        width: 68.75em;
        padding: 3.125em 4.25em;
        max-width: calc(100% - 2.5em);
        background-color: #fff;
    }
    &__close{
        position: absolute;
        top: 1.25em;
        right: 1.25em;
        width: 2.1875em;
        img{
            width: 100%;
            display: block;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
}

.modal-form{
    margin-top: 2.81em;
    display: grid;
    grid-template-columns: calc(100% - 26.125em) 24.0625em;
    justify-content: space-between;
    &__btn{
        height: 5em;
        border-radius: 0.625em;
        text-transform: uppercase;
        font-weight: 600;
        span{
            font-size: 2.25em;
            letter-spacing: -.04em;
        }
        img{
            width: 2.25em;
            margin-right: 1.25em;
        }
    }
}

.buy{
    position: relative;
    &._success{
        .buy__view1{
            opacity: 0;
            pointer-events: none;
        }
        .buy__view2{
            opacity: 1;
            pointer-events: unset;
        }
    }
    &__view2{
        @include centerAbsolute;
        max-width: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        text-align: center;
        opacity: 0;
        pointer-events: none;
        img{
            width: 6.625em;
            display: block;
            margin-bottom: 1.5em;
        }
    }
}

.gallery-modal{
    .modal__dialog{
        width: 80em;
        padding: 11.25em 5.31em;
    }
    &__items{
        display: grid;
        grid-template-columns: repeat(5,calc(20% - 2em));
        gap: 2.5em;
    }
    &__grid{
        display: grid;
        grid-template-columns: calc(100% - 40em) 36.25em;
        justify-content: space-between;
        align-items: center;
    }
}

.gallery-item2{
    cursor: pointer;
    text-align: center;
    .gallery-item__img{
        @include imgCover(123.7%);
        margin-bottom: 1.25em;
        &:before{
            z-index: 2;
        }
    }
    &__top{
        position: relative;
    }
    &__count{
        position: absolute;
        z-index: 3;
        pointer-events: none;
        bottom: .5em;
        left: .875em;
        font-weight: 500;
        color: #fff;
    }
    &__bottom{
        padding: 0 1em;
    }
}

.gallery-slider{
    overflow: hidden;
    &__img{
        padding: 0 2px;
        height: 24.0625em;
        display: flex;
        justify-content: center;
        img{
            max-width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .swiper-slide{
        //width: calc(100% + 2px);
    }
    .slider-nav{
        display: flex;
        justify-content: center;
        margin-top: 1.25em;
        .swiper-pagination{
            margin: 0 1.91em;
            font-size: 1em;
            line-height: 160%;
            font-weight: 500;
            letter-spacing: 0.05em;
        }
    }

}


@media screen and (max-width: 1280px){
    .modal{
        &__close{
            svg{
                width: 2.25em;
            }
            span{
                font-size: 1.25em;
            }
        }
        &__dialog{
            padding: 3.875em 2.5em 3em;
        }
    }

    .modal2{
        .modal__dialog{
            width: 65.625em;
            padding: 2.94em 3.06em;
        }
    }

    .modal-form{
        grid-template-columns: calc(100% - 25.375em) 22.5em;
    }

    .gallery-modal{
        .modal__dialog{
            width: 75em;
            padding: 8.94em 2.81em;
        }
    }
}


@media screen and (max-width: 992px) {
    .modal {
        &:after {
            display: none;
        }
        &__dialog {
            max-width: calc(100% - 2.5em);
            padding: 1.94em 1.875em 3.5em;
        }
        &__close{
            svg{
                margin-right: 1.25em;
                width: 2em;
            }
            span{
                font-size: 1em;
            }
        }
        &__info{
            max-width: 43.5em;
            margin-top: 3.125em;
        }
    }

    .modal2{
        .modal__dialog{
            padding: 3.5em 6.88em;
        }
    }

    .modal-form{
        grid-template-columns: 100%;
        gap: 1.875em;
        margin-top: 1em;
        &__btn{
            span{
                font-size: 1.75em;
            }
        }
    }

    .buy{
        &__view2{
            img{
                margin-bottom: 1.875em;
            }
        }
    }

    .gallery-modal{
        .modal__dialog{
            padding: 7.5em 3.125em;
        }
        &__items{
            grid-template-columns: repeat(5,calc(20% - 7.5em / 5));
            gap: 1.875em;
        }
        &__grid{
            grid-template-columns: calc(100% - 29.3125em) 27.3125em;
        }
    }

    .gallery-slider{
        &__img{
            height: 18.1875em;
        }
    }
}


@media screen and (max-width: 768px){
    .modal{
        &__dialog{
            padding-bottom: 3.25em;
        }
        &__info{
            max-width: 34.375em;
            margin-top: 2.5em;
        }
    }

    .modal2{
        .modal__dialog{
            padding: 3.06em 3.75em;
        }
    }

    .modal-form{
        margin-top: .375em;
        &__btn{
            span{
                font-size: 1.375em;
            }
        }
    }

    .gallery-modal{
        .modal__dialog{
            padding: 6.75em 2.84em;
        }
        &__items{
            grid-template-columns: repeat(5,calc(20% - 1em));
            gap: 1.25em;
        }
        &__grid{
            grid-template-columns: calc(100% - 20.375em) 18.375em;
        }
    }

    .gallery-item2{
        &__bottom{
            padding: 0;
        }
    }

    .gallery-slider{
        &__img{
            height: 12.1875em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 1.94em 1.25em;
        }
        &__info{
            .h2{
                font-size: 2.25em;
            }
        }
    }

    .modal2{
        .modal__dialog{
            padding: 3.75em 1.06em 1.19em;
        }
    }

    .modal-form{
        gap: 1.56em;
        &__btn{
            span{
                font-size: 1.625em;
            }
        }
    }

    .buy{
        &__view2{
            img{
                margin-bottom: .9375em;
                width: 4.75em;
            }
        }
    }

    .gallery-modal{
        .modal__dialog{
            padding: 5.33em 1.25em 1.77em;
        }
        &__items{
            grid-template-columns: repeat(2,calc(50% - .9375em / 2));
            gap: 1.25em .9375em;
        }
        &__grid{
            grid-template-columns: 100%;
            gap: 1.5em;
        }
    }

    .gallery-item2{
        &__img{
            margin-bottom: 1em;
        }
        &__bottom{
            padding: 0 .75em;
        }
    }

    .gallery-slider{
        order: -1;
        &__img{
            height: 22.8125em;
        }
    }
}
