@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-orange{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}

.m-btn-orange-outline{
    color: $primary;
    border: 2px solid $primary;
    &:hover{
        color: #fff;
        background-color: $primary;
        path{
            stroke: #fff;
        }
    }
}
