.contest2{
  padding: 2.5em 0 1.25em;
  background-color: $gray;
  &__icon{
    display: block;
    width: 6.25em;
    margin: 0 auto 1.875em;
  }
  &__grid{
    margin-top: 3.75em;
    display: grid;
    grid-template-columns: calc(100% - 35.9375em - 5.125em) 35.9375em;
    justify-content: space-between;
    padding-bottom: 7.13em;
  }
  &__text{
    max-width: 36.8125em;
    p{
      line-height: inherit;
      @include lastM(0 0 .5em);
    }
  }
  &__text2{
    margin-top: 1.25em;
    font-weight: 600;
    text-transform: uppercase;
  }
}

.contest-slider{
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-left: 1px;
      padding-right: 1px;
    }
    &-button{
      margin: 0;
      top: 50%;
      &-prev{
        left: 0;
        transform: translate(-50%,-50%);
      }
      &-next{
        right: 0;
        transform: translate(50%,-50%);
      }
    }
  }
  &__img{
    border-radius: 0.9375em;
    display: block;
    @include imgCover(123.5%);
    &:before{
      content: '';
      position: absolute;
      border-radius: inherit;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 63.42%, #000 100%);
    }
    img{
      border-radius: inherit;
      width: 100%;
      display: block;
      object-fit: cover;
      min-height: 100%;
    }
  }
  &__hint{
    position: absolute;
    right: -.57em;
    top: -2.68em;
    z-index: 3;
    width: 17.625em;
    img{
      width: 100%;
      display: block;
    }
    &-text{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 3.125em 2em;
      font-family: $font2;
      text-align: center;
      color: #fff;
      span{
        font-size: 3.125em;
        line-height: 101%;
        letter-spacing: -0.02em;
      }
    }
  }
}

.contest-text{
  position: absolute;
  width: 100%;
  padding: 1.25em 3.625em 1.25em 1.25em;
  left: 0;
  bottom: 0;
  z-index: 2;
  color: #FFF;
  font-weight: 600;
  pointer-events: none;
  p{
    line-height: inherit;
    @include lastM(0 0 .75em);
  }
}

.contest-hint{
  margin-top: 2.125em;
  width: 30em;
  position: absolute;
  right: -1.12em;
  top: 100%;
  &__text{
    font-size: 2.25em;
    line-height: 110%;
    font-weight: 700;
    color: $primary;
    font-family: $font2;
  }
  &__img{
    position: absolute;
    right: 100%;
    top: -1.5em;
    margin-right: 1.2em;
    width: 4.25em;
    ._view2,
    ._view3,
    ._view4{
      display: none;
    }
    img{
      display: block;
      width: 100%;
    }
  }
}

.contest-advantages{
  margin-top: 5.06em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.75em));
  gap: 3.5em;
}

.advantage-item{
  display: flex;
  align-items: center;
  font-weight: 600;
  @include lastM(0 0 1.25em);
  &__img{
    width: 4.0625em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &._big{
    text-transform: uppercase;
    margin-left: -1.3em;
    .advantage-item__img{
      width: 6.5625em;
    }
    .text16{
      font-size: 1.375em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 1280px){
  .contest2{
    padding-top: 2.8em;
    &__icon{
      margin-bottom: 1.53em;
    }
    &__grid{
      grid-template-columns: calc(100% - 36.9375em) 35.9375em;
    }
  }

  .contest-advantages{
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .contest2{
    padding: 1.875em 0 2.1em;
    &__icon{
      margin-bottom: .94em;
      width: 4.75em;
    }
    &__grid{
      grid-template-columns: calc(100% - 28.1875em) 27.1875em;
      padding-bottom: 0;
    }
    &__col1{
      max-width: 27.6875em;
    }
    &__text2{
      max-width: 26.6em;
    }
  }

  .contest-slider{
    &__hint{
      width: 14.25em;
      right: -.93em;
      top: -3.75em;
      &-text{
        span{
          font-size: 2.5em;
        }
      }
    }
  }

  .contest-text{
    padding: .56em;
  }

  .contest-hint{
    right: 100%;
    top: unset;
    bottom: 4.44em;
    width: 25.8125em;
    &__img{
      top: 100%;
      right: 1em;
      margin: .75em 0 0;
      width: 3.8125em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__text{
      font-size: 1.875em;
    }
  }

  .contest-advantages{
    width: 47.75em;
    max-width: 100%;
    margin: 3.75em auto 0;
  }

  .advantage-item{
    @media screen and (min-width: 767px){
      &:not(._big){
        br{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 768px){
  .contest2{
    padding: 1.56em 0 1.8em;
    &__icon{
      margin-bottom: 1.25em;
      width: 3.5em;
    }
    &__grid{
      margin-top: 3.125em;
      grid-template-columns: calc(100% - 21.3125em) 20.3125em;
    }
    &__col1{
      max-width: 21.6875em;
    }
  }

  .contest-slider{
    .swiper-button{
      top: 12em;
    }
    &__hint{
      width: 10.13em;
      right: -.85em;
      top: -2.87em;
      &-text{
        padding: 2.5em 1em;
        span{
          font-size: 1.5625em;
        }
      }
    }
  }

  .contest-text{
    position: static;
    margin-top: 3.125em;
    width: calc(100vw - 2.5em);
    margin-left: calc((100vw - 21.3125em - 1.25em) * -1);
    padding: 1.375em;
    color: $black;
    background-color: #fff;
    border-radius: 0.9375em;
  }

  .contest-hint{
    margin-right: 8.5em;
    bottom: 12.5em;
    width: 14.9375em;
    &__img{
      top: 0;
      right: unset;
      left: 100%;
      margin: 0 0 0 .69em;
      width: 5.125em;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__text{
      font-size: 1.5625em;
    }
  }
}


@media screen and (max-width: 576px){
  .contest2{
    padding-bottom: 1.56em;
    &__grid{
      margin-top: 1.875em;
      grid-template-columns: 100%;
      gap: 2.9375em;
    }
    &__col2{
      padding: 0 1.25em;
    }
  }

  .contest-slider{
    &__hint{
      width: 9.7em;
      right: -1.1em;
      top: -3.56em;
    }
    &__img{
      border-radius: 0.625em;
    }
  }

  .contest-text{
    margin: 1.875em 0 0 -1.25em;
    width: calc(100% + 2.5em);
    padding: .75em 1em;
    border-radius: 0.625em;
  }

  .contest-hint{
    position: relative;
    margin: 1.56em 0 0 -1.25em;
    bottom: unset;
    left: unset;
    right: unset;
    width: 14.5em;
    &__img{
      top: 0;
      margin: -.875em 0 0 -.69em;
      width: 3.5em;
      ._view3{
        display: none;
      }
      ._view4{
        display: block;
      }
    }
  }

  .contest-advantages{
    margin-top: 3.125em;
    padding: 0 .44em 0 1.25em;
    grid-template-columns: 100%;
    gap: 1.25em;
  }

  .advantage-item{
    &._big{
      margin-left: -1.25em;
      .text16{
        font-size: 1.625em;
        line-height: 120%;
      }
    }
  }
}
