.yachts{
  padding: 6.75em 0 7.5em;
  background-color: $black;
  color: #fff;
  &__top{
    max-width: 64.5em;
    width: 100%;
    margin: 0 auto;
    @include grid100gap(.94em);
  }
  &__items{
    margin-top: 4.12em;
    @include grid100gap(7.5em);
  }
}

.yacht{
  &._type2{
    @media screen and (min-width: 993px){
      .yacht-scheme{
        &__col1{
          padding-top: 6.8em;
          .yacht-desc{
            &:nth-child(4){
              &:before{
                width: 3em;
              }
              &:after{
                margin-left: 3.125em;
              }
            }
            &:nth-child(5){
              &:before{
                width: 9em;
              }
              &:after{
                margin-left: 9.125em;
              }
            }
          }
        }
        &__col2{
          padding-top: 2.47em;
          .yacht-desc{
            &:nth-child(1){
              &:before{
                width: 15em;
              }
              &:after{
                margin-right: 15.125em;
              }
            }
            &:nth-child(2){
              &:before{
                width: 12.5em;
              }
              &:after{
                margin-right: 12.625em;
              }
            }
            &:nth-child(3){
              &:before{
                width: 9em;
              }
              &:after{
                margin-right: 9.125em;
              }
            }
            &:nth-child(4){
              &:before{
                width: 6.5em;
              }
              &:after{
                margin-right: 6.625em;
              }
            }
            &:nth-child(5){
              &:before{
                width: 3.5em;
              }
              &:after{
                margin-right: 3.625em;
              }
            }
          }
        }
        &__img{
          width: 30.4em;
        }
      }
    }
  }
  &._type3{
    @media screen and (min-width: 993px){
      .yacht-scheme{
        &__col1{
          padding-top: 1.4em;
          .yacht-desc{
            &:nth-child(4){
              &:before{
                width: 4em;
              }
              &:after{
                margin-left: 4.125em;
              }
            }
            &:nth-child(5){
              &:before{
                width: 2em;
              }
              &:after{
                margin-left: 2.125em;
              }
            }
          }
        }
        &__col2{
          padding-top: 4.125em;
          .yacht-desc{
            &:nth-child(1){
              &:before{
                width: 15em;
              }
              &:after{
                margin-right: 15.125em;
              }
            }
            &:nth-child(2){
              &:before{
                width: 10.5em;
              }
              &:after{
                margin-right: 10.625em;
              }
            }
            &:nth-child(3){
              &:before{
                width: 7em;
              }
              &:after{
                margin-right: 7.125em;
              }
            }
            &:nth-child(4){
              &:before{
                width: 4em;
              }
              &:after{
                margin-right: 4.125em;
              }
            }
            &:nth-child(5){
              &:before{
                width: 2.5em;
              }
              &:after{
                margin-right: 2.625em;
              }
            }
          }
        }
        &__img{
          width: 30.8125em;
        }
      }
    }
  }
}

.yacht-info{
  position: relative;
  &__top,
  &__desc{
    width: calc(100% - 7.375em);
    margin-left: auto;
  }
  &__top{
    border-bottom: 1px solid #fff;
    display: flex;
    justify-content: space-between;
    .custom-title{
      transform: translateY(-.175em);
    }
  }
  &__num{
    position: absolute;
    left: 0;
    top: .31em;
    width: 4.375em;
    height: 4.375em;
    border-radius: 50%;
    @include center;
    background-color: $primary;
    font-weight: 600;
  }
  &__desc{
    margin-top: 1em;
    .text22{
      max-width: 45.1em;
    }
  }
}

.yacht-scheme{
  margin-top: 1.875em;
  position: relative;
  &__img{
    width: 21.38em;
    margin: 0 auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__col{
    width: 22em;
  }
  &__col1{
    padding-top: 7em;
    .yacht-desc{
      &:nth-child(1){
        &:before{
          width: 10.5em;
        }
        &:after{
          margin-left: 10.625em;
        }
      }
      &:nth-child(2){
        &:before{
          width: 8.125em;
        }
        &:after{
          margin-left: 8.25em;
        }
      }
    }
  }
  &__col2{
    padding-top: 4em;
    .yacht-desc{
      justify-content: unset;
      text-align: left;
      img{
        order: -1;
        margin: 0 1.25em 0 0;
      }
      &:before,
      &:after{
        left: unset;
        right: 100%;
      }
      &:after{
        margin: 0 5.625em 0 0;
      }
      &:nth-child(1){
        &:before{
          width: 13.5em;
        }
        &:after{
          margin-right: 13.625em;
        }
      }
      &:nth-child(2){
        &:before{
          width: 10em;
        }
        &:after{
          margin-right: 10.125em;
        }
      }
      &:nth-child(3){
        &:before{
          width: 6.5em;
        }
        &:after{
          margin-right: 6.625em;
        }
      }
    }
  }
}

.yacht-desc{
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  text-align: right;
  position: relative;
  @include lastM(0 0 2.5em);
  &:before,
  &:after{
    content: '';
    position: absolute;
    left: 100%;
    top: 2.0625em;
    transform: translateY(-50%);
  }
  &:before{
    width: 5.5em;
    height: 2px;
    border-bottom: 2px dashed $primary;
  }
  &:after{
    margin-left: 5.625em;
    width: .5em;
    height: .5em;
    background-color: $primary;
    border-radius: 50%;
  }
  img{
    width: 4.125em;
    flex-shrink: 0;
    margin-left: 1.25em;
  }
  &__text{
    min-height: 4.125em;
    display: flex;
    align-items: center;
    .text16{
      line-height: 140%;
    }
  }
}

.yacht-hint{
  padding: 1.25em 6.65em;
  text-align: center;
  margin-top: 3.125em;
  position: relative;
  width: calc(100% - 1em);
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid $primary;
    border-radius: 1em;
    transform: skew(-15deg, 0)
  }
  p{
    line-height: inherit;
    display: inline;
  }
}


@media screen and (max-width: 1280px){
  .yachts{
    &__top{
      gap: 1.25em;
      max-width: 60em;
    }
    &__items{
      margin-top: 4em;
    }
  }

  .yacht-scheme{
    &__img{
      transform: translateX(-2em);
    }
    &__col1{
      width: 17.1875em;
    }
  }

  .yacht-hint{
    padding: 1.56em 4.19em;
  }
}


@media screen and (max-width: 992px){
  .yachts{
    &__top{
      gap: 1.5em;
    }
    &__items{
      margin-top: 3.75em;
    }
  }

  .yacht-info{
    &__top,
    &__desc{
      width: calc(100% - 5.1875em);
    }
    &__top{
      .custom-title{
        transform: translateY(-.15em);
      }
    }
    &__num{
      top: .06em;
      width: 3.4375em;
      height: 3.4375em;
    }
  }

  .yacht-scheme{
    margin-top: 3.125em;
    &__img{
      transform: unset;
    }
    &__info{
      position: static;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
      margin-top: 2em;
    }
    &__col{
      padding: 0;
      width: 100%;
    }
  }

  .yacht-desc{
    justify-content: flex-start;
    text-align: left;
    @media screen and (min-width: 577px){
      &:before,
      &:after{
        display: none;
      }
    }
    img{
      order: -1;
      margin: 0 1.25em 0 0;
    }
  }

  .yacht-hint{
    border-radius: .5em;
    padding: 1.37em 3.5em;
    p{
      display: block;
      @include lastM(0 0 1em);
    }
    &:before{
      transform: skew(-10deg,0);
    }
  }
}


@media screen and (max-width: 768px){
  .yachts{
    padding: 5.7em 0 6.25em;
    &__top{
      gap: 1.31em;
    }
    &__items{
      margin-top: 3.25em;
      gap: 6.25em;
    }
  }

  .yacht-info{
    &__num{
      .text36{
        @media screen and (min-width: 577px){
          font-size: 2.25em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .yachts{
    &__top{
      .text22{
        padding: 0 1em;
      }
    }
    &__items{
      margin-top: 2.75em;
    }
  }

  .yacht-info{
    padding-bottom: 4.625em;
    &__top,
    &__desc{
      width: 100%;
    }
    &__top{
      align-items: center;
      justify-content: unset;
      padding-bottom: .69em;
    }
    &__num{
      position: static;
      margin-right: 1.25em;
      width: 2.8125em;
      height: 2.8125em;
      flex-shrink: 0;
      .text36{
        font-size: 1.625em;
      }
    }
    &__desc{
      margin-top: .69em;
    }
    .custom-title{
      position: absolute;
      bottom: 0;
      left: 0;
      .h2{
        font-size: 2.25em;
      }
    }
  }

  .yacht-scheme{
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      transform: translateX(-50%);
    }
    &:before{
      width: 1.5px;
      height: calc(100% - 2.0625em);
      border-left: 2px dashed $primary;
    }
    &:after{
      width: .5em;
      height: .5em;
      border-radius: 50%;
      background-color: $primary;
      top: 0;
      left: 0;
    }
    &__img{
      width: 14.0625em;
      margin: 0 0 0 1.5em;
    }
    &__info{
      grid-template-columns: 100%;
      gap: 2.19em;
      margin-top: 1.875em;
      padding-left: 3em;
    }
  }

  .yacht-desc{
    @include lastM(0 0 2.19em);
    &:before,
    &:after{
      left: unset;
      right: 100%;
    }
    &:before{
      width: 3em !important;
      height: 1.5px;
    }
    &:after{
      margin-right: 3em !important;
      transform: translate(50%,-50%);
    }
  }

  .yacht-hint{
    border-radius: .125em;
    padding: 1.5em 1.19em;
    width: 100%;
    &:before{
      transform: skew(0);
    }
  }
}
