.experience2{
  padding: 3.8em 0 8em;
  &__items{
    margin-top: 3.75em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 6.25em / 3));
    gap: 3.125em;
  }
  &__bottom{
    margin-top: 3.25em;
    display: grid;
    grid-template-columns: calc(100% - 30em) 29.375em;
    gap: .625em;
    .experience-item{
      padding: 1.5em;
      background-color: $gray;
      border-radius: 0.9375em;
    }
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: 1.25em;
    margin-top: 3.75em;
  }
}

.experience-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 5.625em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.experience-text{
  grid-row: 1/3;
  padding: 1.81em 1.875em 1.75em;
  border-radius: 0.9375em;
  background-color: $gray;
  .text{
    margin-bottom: .94em;
    p{
      font-size: 1em;
      line-height: 160%;
      letter-spacing: -0.03em;
      @include lastM(0 0 .5em);
    }
  }
}


@media screen and (max-width: 1280px){
  .experience-text{
    .text{
      p{
        line-height: 150%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .experience2{
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 4.4em / 3));
      gap: 2.2em;
    }
    &__bottom{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      .experience-item{
        padding: 1.25em;
      }
    }
  }

  .experience-item{
    &__icon{
      width: 5.125em;
      margin-right: .94em;
    }
  }

  .experience-text{
    grid-row: unset;
    grid-column: 1/-1;
    padding: 1.5em 2.12em;
  }
}


@media screen and (max-width: 768px){
  .experience2{
    padding-top: 3.125em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
      margin-top: 3.125em;
    }
    &__bottom{
      grid-template-columns: 1fr 1.2fr;
      gap: .9375em;
      .experience-item{
        padding: .69em .5em;
      }
    }
    &__img{
      margin-top: 3.125em;
    }
  }

  .experience-item{
    &__icon{
      width: 5.0625em;
    }
  }

  .experience-text{
    padding: 1.31em 1.25em .625em;
  }
}


@media screen and (max-width: 576px){
  .experience2{
    padding-top: 1.94em;
    &__items{
      margin-top: 1.94em;
      grid-template-columns: 100%;
    }
    &__bottom{
      gap: 1.25em;
      grid-template-columns: 100%;
      .experience-item{
        min-height: 7.375em;
        .experience-item__icon{
          width: 3.75em;
        }
        &:nth-child(3){
          padding-right: 0;
          .experience-item__icon{
            width: 3.25em;
          }
        }
      }
    }
    &__img{
      border-radius: .9375em;
    }
  }

  .experience-text{
    padding: 2.25em 1.25em .5em;
    br{
      display: none;
    }
  }
}
