.main{
  padding: 5em 0 3.75em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 37.5em) 37.5em;
  }
  &__col2{
    position: relative;
  }
  &__img{
    position: relative;
    display: block;
  }
  &__play{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 3.5em;
    padding: 0 .6em 0 2.31em;
    height: 4.375em;
    background-color: #fff;
    border-radius: 6.25em;
    font-weight: 600;
    text-transform: uppercase;
    img{
      width: 3.4375em;
      flex-shrink: 0;
      margin-left: .875em;
    }
    span{
      font-size: 1.375em;
      line-height: 150%;
      letter-spacing: -0.03em;
    }
  }
}

.avatar{
  padding-bottom: 100%;
  background: linear-gradient(195deg, #6855CE 2.91%, #A728B7 16.04%, #C31B6D 28.09%, #F64D48 43.16%, #F75343 54.9%, #FD9C34 70.27%, #FFBC57 80.51%, #F54059 98.78%, rgba(255, 138, 0, 0.00) 104.16%);
  border-radius: 50%;
  position: relative;
  &__inner{
    position: absolute;
    @include centerAbsolute;
    width: calc(100% - .75em);
    height: calc(100% - .75em);
    background-color: #fff;
    border-radius: 50%;
    padding: .5625em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: inherit;
    }
  }
}

.main-slider{
  max-width: 35.3125em;
  margin-top: .3em;
  .swiper-container{
    overflow: hidden;
  }
}

.slider-nav{
  display: inline-flex;
  align-items: center;
  margin-top: .75em;
  position: relative;
  &__hint{
    position: absolute;
    top: -.75em;
    left: 100%;
    margin-left: 1em;
    width: 9em;
  }
  .swiper-button{
    position: static;
  }
  .swiper-pagination{
    position: static;
    width: auto;
    margin: 0 .625em;
  }
}

.swiper-button{
  width: 2.1875em;
  height: 2.1875em;
  border-radius: 50%;
  margin: 0;
  svg{
    width: 1.375em;
  }
  &.swiper-button-disabled{
    background-color: $gray;
    opacity: 1;
  }
}

.swiper-pagination{
  @include center;
  &-bullet{
    opacity: 1;
    background-color: $gray;
    margin: 0 .5em !important;
    width: .625em;
    height: .625em;
    &-active{
      background-color: $black;
    }
  }
}

.main-info{
  margin-top: 3.5em;
  max-width: 36.6875em;
  .text{
    margin-top: .3125em;
  }
}

.avatar2{
  padding-bottom: 100%;
  &__inner{
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 50%;
    padding: .5em;
    border: 2px dashed $primary;
    position: absolute;
    img{
      border-radius: inherit;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
}


@media screen and (max-width: 1280px){
  .main{
    padding: 4.6em 0 3.75em;
    &__inner{
      grid-template-columns: calc(100% - 35.3125em) 35.3125em;
    }
    &__play{
      padding: 0 .6em 0 2.31em;
      height: 4.0625em;
      img{
        width: 3.125em;
        margin-left: 1.1em;
      }
      span{
        font-size: 1.25em;
      }
    }
  }

  .main-slider{
    max-width: 33.12em;
  }

  .main-info{
    margin-top: 3.25em;
    max-width: 33em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 2.92em 0 3.45em;
    &__inner{
      grid-template-columns: calc(100% - 26.5625em) 26.5625em;
    }
    &__img{
      padding-top: .77em;
    }
    &__play{
      padding: 0 .43em 0 1.45em;
      height: 3.125em;
      bottom: 2.44em;
      img{
        width: 2.5em;
        margin-left: .625em;
      }
      span{
        font-size: 1em;
      }
    }
  }

  .avatar{
    &__inner{
      width: calc(100% - .625em);
      height: calc(100% - .625em);
      padding: .3125em;
    }
  }

  .main-slider{
    max-width: 28.5em;
  }

  .swiper-nav{
    margin-top: 2.42em;
  }

  .main-info{
    margin-top: 2.875em;
    max-width: 28.6875em;
  }

  .avatar2{
    &__inner{
      padding: .25em;
    }
  }
}


@media screen and (max-width: 768px){
  .main{
    padding: 2.5em 0 3.38em;
    &__inner{
      grid-template-columns: calc(100% - 20.9375em) 20.9375em;
    }
    &__img{
      padding-top: .625em;
    }
    &__play{
      bottom: 2.69em;
    }
    .h1{
      font-size: 3.125em;
      letter-spacing: -.04em;
    }
  }

  .avatar{
    &__inner{
      width: calc(100% - .375em);
    }
  }

  .main-slider{
    max-width: 22.125em;
  }

  .slider-nav{
    margin-top: 2em;
    font-size: .75em;
  }

  .main-info{
    margin-top: 6.72em;
    width: calc(100% + 18em);
    max-width: unset;
    .text{
      max-width: 38em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 2.25em 0 3.4em;
    &__inner{
      grid-template-columns: 100%;
      padding-top: 4.8em;
      position: relative;
      gap: 1.5em;
    }
    &__col2{
      order: -1;
    }
    &__top{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
    &__img{
      padding-top: 0;
    }
    &__play{
      bottom: 2.37em;
      height: 2.8125em;
      padding: 0 .25em 0 1.25em;
      img{
        width: 2.3125em;
        margin-left: .44em;
      }
    }
  }

  .slider-nav{
    font-size: 1em;
  }

  .main-info{
    margin-top: 4.78em;
    width: 100%;
    .text{
      margin-top: .625em;
    }
  }
}
