.author{
  padding: 3.75em 0 5.625em;
  background-color: $gray;
  &__inner{
    width: 60.75em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__img{
    width: 12.1875em;
    height: 12.1875em;
    border-radius: 50%;
    margin: 0 auto;
    padding: .625em;
    border: 2px dashed $primary;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__quote{
    text-align: center;
    font-style: italic;
    margin-top: 1.875em;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      @include bgImgSize(100%);
      width: 3.1875em;
      height: 2.375em;
    }
    &:before{
      background-image: url(../img/quote.svg);
      left: 0;
      margin: 0 0 1.81em -2.39em;
      bottom: 100%;
    }
    &:after{
      background-image: url(../img/quote2.svg);
      right: 0;
      margin: 0 -2.39em -1.92em 0;
      bottom: 0;
    }
  }
}

.author2{
  .author__inner{
    width: 35.1875em;
  }
}


@media screen and (max-width: 1280px){
  .author{
    &__inner{
      width: 55.5em;
    }
    &__quote{
      margin-top: 1.25em;
      &:before,
      &:after{
        margin: 0;
      }
      &:before{
        left: unset;
        right: 100%;
        margin-right: 1.25em;
        margin-bottom: 0;
        bottom: 100%;
      }
      &:after{
        margin-bottom: -1.8em;
        margin-left: 1.25em;
        right: unset;
        left: 100%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .author{
    padding-bottom: 6.125em;
    &__inner{
      width: 44.25em;
    }
  }
}


@media screen and (max-width: 768px){
  .author{
    padding-top: 3.125em;
    &__inner{
      width: 38.5em;
    }
    &__quote{
      &:before{
        margin-right: .39em;
        margin-bottom: .39em;
      }
      &:after{
        margin-left: .43em;
        bottom: unset;
        top: 100%;
        margin-bottom: 0;
        margin-top: .61em;
      }
    }
  }

  .author2{
    .author__inner{
      width: 31.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .author{
    padding-bottom: 5.81em;
    &__inner{
      width: 19.625em;
    }
    &__img{
      width: 9.6875em;
      height: 9.6875em;
      padding: .5em;
    }
    &__quote{
      &:before,
      &:after{
        width: 2.3125em;
        height: 1.75em;
      }
      &:before{
        margin-right: -1.1em;
        margin-bottom: 0;
      }
      &:after{
        margin-left: -1.1em;
        margin-top: .39em;
      }
    }
  }

  .author2{
    .author__quote{
      &:before{
        margin-right: -2.3125em;
      }
      &:after{
        margin-left: -2.3125em;
      }
    }
  }
}
