.h1{
  font-size: 5em;
  font-weight: 600;
  line-height: 110%;
  letter-spacing: -0.02em;
  text-transform: uppercase;
}

.h2{
  font-size: 3.375em;
  line-height: 130%;
  font-weight: 600;
  letter-spacing: -0.03em;
  text-transform: uppercase;
  &.text-capitalize{
    text-transform: unset;
  }
}

.text36{
  font-size: 2.25em;
  line-height: 120%;
  letter-spacing: -.04em;
}

.text p,
.text22{
  font-size: 1.375em;
  line-height: 150%;
  letter-spacing: -0.03em;
}

.text{
  hr{
    height: 2em;
    opacity: 0;
    display: block;
  }
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 160%;
  letter-spacing: -0.03em;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.orange-text{
  color: $primary;
}

.red-text{
  color: #F24D4D;
}

.green-text{
  color: #69A042;
}

.text-center{
  text-align: center;
}

.text{
  p{
    @include lastM(0 0 .25em);
  }
  mark,a{
    color: $primary;
  }
  mark{
    background-color: transparent;
  }
  a{
    text-decoration: underline;
    cursor: pointer;
  }
}


@media screen and (max-width: 1280px){
  .h1{
    font-size: 4.6875em;
  }

  .h2{
    font-size: 3em;
  }

  .text36{
    font-size: 2.0625em;
  }

  .text p,
  .text22{
    font-size: 1.25em;
  }

  .text16{
    line-height: 150%;
  }
}



@media screen and (max-width: 992px){
  .h1{
    font-size: 4.375em;
  }

  .h2{
    font-size: 2.75em;
  }

  .text36{
    font-size: 1.75em;
  }

  .text p,
  .text22{
    font-size: 1em;
    line-height: 150%;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }
}


@media screen and (max-width: 768px) {
  .h1 {
    font-size: 3.125em;
  }

  .h2{
    font-size: 2.25em;
  }

  .text36{
    font-size: 1.375em;
  }

  .text22 {
    font-size: 1em;
  }
}



@media screen and (max-width: 576px) {
  .h1 {
    font-size: 2.25em;
  }

  .h2{
    font-size: 1.625em;
  }

  .text36{
    font-size: 1.625em;
  }

  .text16-mob{
    font-size: 1em;
  }
}
