.faq{
  padding: 7.5em 0;
  background-color: $gray;
  &__top{
    text-align: center;
    @include grid100gap(1.5em);
  }
  &__items{
    margin: 2.5em auto 0;
    width: 70em;
    max-width: 100%;
  }
}

.qa-card{
  &._active{
    .qa-card__btn{
      background-color: $black;
      path{
        fill: #fff;
      }
      svg{
        transform: rotate(90deg);
      }
    }
  }
  &__show{
    padding: 1.25em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid $black;
    cursor: pointer;
  }
  &__hidden{
    padding-bottom: 1.875em;
  }
  &__btn{
    margin-left: 3em;
    width: 3.75em;
    height: 3.75em;
    flex-shrink: 0;
    border: 2px solid $black;
    border-radius: 50%;
    @include center;
    &:hover{
      background-color: $black;
      path{
        fill: #fff;
      }
    }
    svg{
      width: 1.25em;
    }
  }
  &__hidden{
    display: none;
    padding: 1.25em 0 1.875em;
  }
  .text{
    max-width: 57.25em;
    p{
      font-size: 1em;
      line-height: 160%;
      letter-spacing: -0.03em;
      @include lastM(0 0 .5em);
    }
  }
}


@media screen and (max-width: 1280px){
  .qa-card{
    .text{
      p{
        line-height: 150%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .qa-card{
    &__show{
      padding: .94em 0;
    }
    &__btn{
      width: 3.125em;
      height: 3.125em;
      svg{
        width: 1.125em;
      }
    }
    &__hidden{
      padding: .94em 0 1.5em;
    }
  }
}


@media screen and (max-width: 768px){
  .faq{
    padding: 6.25em 0;
  }

  .qa-card{
    &__show{
      padding: .625em 0;
    }
    &__hidden{
      padding: .625em 0 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .faq{
    &__top{
      gap: .9375em;
    }
  }

  .qa-card{
    &__show{
      padding: .94em 0;
    }
    &__btn{
      width: 2.8125em;
      height: 2.8125em;
      svg{
        width: 1em;
      }
    }
    &__hidden{
      padding: .94em 0;
    }
  }
}
