.tariffs{
  padding: 7.5em 0;
  &__grid{
    width: calc(100% + 3.75em);
    margin: 4em 0 0 -1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
}

.tariff-card{
  padding: 0 1.875em 6.56em;
  border-left: 1px solid #F4F4F4;
  display: flex;
  flex-direction: column;
  position: relative;
  @media screen and (min-width: 993px){
    &:nth-child(3n - 2){
      border-left: none;
    }
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    max-width: 15em;
  }
  &__icon{
    width: 4.0625em;
    flex-shrink: 0;
    margin-left: 2em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__prices{
    margin-top: 1.56em;
    min-height: 7.125em;
    display: inline-block;
    position: relative;
    margin-right: auto;
    &-hint{
      position: absolute;
      display: flex;
      align-items: center;
      bottom: -.625em;
      left: 100%;
      margin-left: 1.5em;
      font-weight: 700;
      font-family: $font2;
      color: $primary;
      width: 12em;
      img{
        margin-right: 1em;
        width: 3.1875em;
      }
      span{
        font-size: 1.5625em;
        line-height: 110%;
      }
    }
  }
  &__price2{
    opacity: 0.4;
    text-decoration: line-through;
  }
  &__items{
    margin-top: 3.125em;
    @include grid100gap(.94em);
  }
  &__item{
    display: flex;
    align-items: flex-start;
    img{
      width: 2.25em;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
    &-text{
      font-size: 1em;
      line-height: 160%;
      letter-spacing: -0.03em;
      min-height: 2.25em;
      display: flex;
      align-items: center;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 3.44em;
    display: flex;
    flex-direction: column;
    min-height: 9.8em;
  }
  &__available{
    min-height: 3.75em;
    padding: .5em 1.13em;
    display: flex;
    align-items: center;
    border-radius: 0.625em;
    background-color: $gray;
    font-weight: 600;
    text-transform: uppercase;
  }
  &__hint{
    margin-top: .94em;
  }
  &__btn{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% - 3.75em);
    height: 5em;
    border-radius: 0.625em;
    text-transform: uppercase;
    font-weight: 600;
    span{
      font-size: 2.25em;
      letter-spacing: -.04em;
    }
    img{
      width: 2.25em;
      margin-right: 1.25em;
    }
  }
}


@media screen and (max-width: 1280px){
  .tariffs{
    padding: 7.5em 0;
  }

  .tariff-card{
    &__prices{
      min-height: 6.5em;
      &-hint{
        bottom: -1.3125em;
        margin-left: 0;
      }
    }
    &__items{
      margin-top: 3.81em;
    }
    &__bottom{
      padding-top: 3.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .tariffs{
    &__grid{
      width: 45em;
      margin: 3.75em auto 0;
      grid-template-columns: 100%;
      gap: 4.375em;
    }
  }

  .tariff-card{
    padding: 0 0 0 1.25em;
    &__top{
      align-items: center;
    }
    &__title{
      max-width: unset;
      .text22{
        font-size: 1.75em;
      }
      @media screen and (min-width: 577px){
        br{
          display: none;
        }
      }
    }
    &__icon{
      width: 3.75em;
    }
    &__prices{
      min-height: unset;
      margin-top: 1.25em;
      &-hint{
        bottom: 0;
        width: 18em;
      }
    }
    &__items{
      margin-top: 2.89em;
    }
    &__bottom{
      padding-top: 2.5em;
      min-height: unset;
    }
    &__btn{
      position: static;
      transform: unset;
      width: 100%;
      margin-top: 1.875em;
      span{
        font-size: 1.75em;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .tariffs{
    padding: 6.25em 0;
    &__grid{
      margin-top: 3.125em;
      width: 39.25em;
    }
  }

  .tariff-card{
    &__title{
      .text22{
        font-size: 1.375em;
      }
    }
    &__prices{
      margin-top: .625em;
      &-hint{
        bottom: -.44em;
      }
    }
    &__price1{
      font-size: 2.75em;
    }
    &__items{
      margin-top: 2.76em;
    }
    &__btn{
      span{
        font-size: 1.375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .tariffs{
    &__grid{
      width: 100%;
    }
  }

  .tariff-card{
    padding-left: .94em;
    &__title{
      max-width: 12em;
      .text22{
        font-size: 1em;
      }
    }
    &__icon{
      width: 3.4375em;
    }
    &__prices{
      margin-top: 1.25em;
      &-hint{
        bottom: .44em;
        width: 10em;
        margin-left: .5em;
        align-items: flex-end;
        span{
          font-size: 1.25em;
        }
      }
    }
    &__price1{
      font-size: 2.25em;
    }
    &__items{
      margin-top: 1.875em;
    }
    &__available{
      padding: .5em .81em;
    }
    &__btn{
      span{
        font-size: 1.625em;
      }
    }
  }
}
