.contest{
  padding: 7.69em 0 2.5em;
  background-color: $gray;
  &__top{
    width: 56.125em;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__grid{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.contest-card{
  display: grid;
  grid-template-columns: calc(100% - 16.75em) 15em;
  gap: 1.75em;
  border-radius: 1.25em;
  background-color: #fff;
  padding: 1.25em;
  &__img{
    @include imgCover(131.25%);
    border-radius: .9375em;
    height: 100%;
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__title{
    text-transform: uppercase;
    font-weight: 600;
  }
  &__desc{
    margin-top: .94em;
  }
  &__ig{
    display: flex;
    align-items: center;
    align-self: flex-start;
    margin-top: auto;
    img{
      margin-right: .625em;
      flex-shrink: 0;
      width: 1.375em;
    }
  }
}


@media screen and (max-width: 1280px){
  .contest{
    padding-bottom: 2.625em;
    &__top{
      width: 50.375em;
    }
  }

  .contest-card{
    grid-template-columns: calc(100% - 14.25em) 14em;
    gap: .25em;
    &__title{
      max-width: 12em;
    }
  }
}


@media screen and (max-width: 992px){
  .contest{
    padding-bottom: 1.875em;
    &__top{
      width: 43.25em;
    }
    &__grid{
      margin-top: 3.31em;
    }
  }

  .contest-card{
    display: flex;
    flex-direction: column;
    &__img{
      order: -1;
      padding-bottom: 78%;
      height: unset;
      flex-shrink: 0;
      margin-bottom: 1.25em;
    }
    &__title{
      max-width: 100%;
    }
    &__desc{
      margin-top: .625em;
      margin-bottom: 2.5em;
    }
  }
}


@media screen and (max-width: 768px){
  .contest{
    padding-bottom: 1.56em;
    &__top{
      width: 34.375em;
    }
    &__grid{
      margin-top: 1.875em;
    }
  }

  .contest-card{
    padding: .81em .94em;
    &__img{
      padding-bottom: 108%;
    }
    &__desc{
      margin-bottom: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .contest{
    padding-top: 6.25em;
    &__top{
      width: 15.625em;
      .custom-title .h2{
        font-size: 1em;
      }
    }
    &__grid{
      margin-top: 1.94em;
      grid-template-columns: 100%;
    }
  }

  .contest-card{
    border-radius: 0.9375em;
    padding: .81em;
    &__img{
      padding-bottom: 86%;
      border-radius: .625em;
    }
    &__desc{
      margin-top: .31em;
    }
  }
}
