.trip{
  padding: 7.5em 0 3.75em;
  &__top{
    padding: 0 1em;
  }
}

.trip-gallery{
  display: grid;
  grid-template-columns: repeat(6,calc(100% / 6 - 3.125em / 6));
  gap: .625em;
  margin-top: 8.75em;
  .gallery-item{
    height: 18.75em;
    min-height: 100%;
  }
  &__item1,
  &__item4,
  &__item5,
  &__item6{
    grid-column: span 2;
  }
  &__item4{
    grid-row: span 2;
  }
  &__item1,
  &__item2,
  &__item3,
  &__item4{
    .trip-gallery__hint{
      bottom: 100%;
      margin-bottom: 1.5em;
      img{
        left: 100%;
        top: 1em;
        margin-left: 1em;
        width: 2.8125em;
      }
    }
  }
  &__item1{
    .trip-gallery__hint{
      left: 1.94em;
    }
  }
  &__item2,
  &__item3,{
    .trip-gallery__hint{
      left: .875em;
    }
  }
  &__item4{
    .trip-gallery__hint{
      left: 4.375em;
    }
  }
  &__item5,
  &__item6{
    .trip-gallery__hint{
      top: 100%;
      margin-top: 1.19em;
      left: 2.75em;
      img{
        left: 100%;
        width: 3.625em;
        top: -.25em;
        margin-left: 1em;
      }
    }
  }
  &__item5{
    .trip-gallery__hint{
      left: 1.12em;
    }
  }
  &__hint{
    position: absolute;
    max-width: 100%;
    &-text{
      font-size: 1.5625em;
      line-height: 110%;
      font-weight: 700;
      color: $primary;
      font-family: $font2;
    }
    img{
      position: absolute;
    }
    ._show-mob{
      @media screen and (min-width: 577px){
        display: none;
      }
    }
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 1280px){
  .trip{
    &__top{
      padding: 0;
    }
  }

  .trip-gallery{
    margin-top: 9.625em;
    .gallery-item{
      height: 17.5em;
    }
    &__item5,
    &__item6{
      .trip-gallery__hint{
        margin-top: 1.94em;
        left: 3.5em;
        img{
          margin-left: 1.38em;
        }
      }
    }
    &__item5{
      .trip-gallery__hint{
        left: 0;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .trip{

  }

  .trip-gallery{
    margin-top: 9.125em;
    .gallery-item{
      height: 13.4375em;
    }
    &__item1,
    &__item2,
    &__item3,
    &__item4{
      .trip-gallery__hint{
        bottom: 100%;
        margin-bottom: 2.06em;
        left: 0;
      }
    }
    &__item5,
    &__item6{
      .trip-gallery__hint{
        margin-top: 1.25em;
        left: 0;
        img{
          margin-left: 1.6em;
        }
      }
    }
    &__hint{
      &-text{
        font-size: 1.375em;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .trip{
    padding: 6.25em 0 3.125em;
  }

  .trip-gallery{
    margin-top: 7.7em;
    .gallery-item{
      height: 10.625em;
    }
    &__item1,
    &__item2,
    &__item3,
    &__item4{
      .trip-gallery__hint{
        margin-bottom: 1.625em;
        img{
          width: 2.25em;
        }
      }
    }
    &__item5,
    &__item6{
      .trip-gallery__hint{
        left: .375em;
        img{
          margin-left: 1.06em;
        }
      }
    }
    &__hint{
      &-text{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .trip{
    .h2{
      font-size: 1.5em;
    }
  }

  .trip-gallery{
    margin-top: 6.7em;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    .gallery-item{
      height: 7em;
    }
    &__item4{
      order: 5;
    }
    &__item6{
      order: 6;
    }
    &__item4,
    &__item5,
    &__item6{
      grid-column: unset;
    }
    &__item1{
      padding-bottom: 5.37em;
      height: unset !important;
    }
    &__item2,
    &__item3{
      padding-bottom: 6.62em;
      height: unset !important;
    }
    &__item1,
    &__item2,
    &__item3,
    &__item4{
      .trip-gallery__hint{
        margin-bottom: 1.5em;
        img{
          width: 2.25em;
          top: .75em;
          margin-left: .5em;
        }
      }
    }
    &__item4{
      .trip-gallery__hint{
        margin-bottom: .94em;
        img{
          top: 1.25em;
          margin-left: .5em;
        }
      }
    }
    &__item6{
      .trip-gallery__hint{
        left: 0;
        img{
          margin-left: .5em;
          top: .31em;
          width: 3.125em;
        }
      }
    }
    &__item5{
      .trip-gallery__hint{
        top: unset;
        bottom: 100%;
        margin-bottom: 1.625em;
        left: 0;
        img{
          width: 2.25em;
          top: .75em;
          margin-left: .5em;
        }
      }
    }
    &__hint{
      &-text{
        font-size: 1.125em;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
