.result{
  padding: 1.25em 0 7.5em;
  background-color: $gray;
  &__icon{
    width: 6em;
    display: block;
    margin: 0 auto 1.875em;
  }
  &__title{
    width: 65.25em;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 3.3em));
    gap: 6.6em;
    margin-top: 3.75em;
  }
}

.result-item{
  display: flex;
  align-items: center;
  &__img{
    width: 8.5em;
    flex-shrink: 0;
    margin-right: 1.56em;
    img{
      border-radius: 50%;
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.31em);
  }
  &__title{
    text-transform: uppercase;
    font-weight: 600;
  }
}


@media screen and (max-width: 1280px){
  .result{
    &__items{
      grid-template-columns: repeat(2,calc(50% - 2.85em));
      gap: 5.7em;
    }
  }
}


@media screen and (max-width: 992px){
  .result{
    padding-top: 1.875em;
    &__title{
      width: 54.375em;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 2.5em;
      margin-top: 2.5em;
    }
  }

  .result-item{
    &__img{
      width: 6.5625em;
    }
  }
}


@media screen and (max-width: 768px){
  .result{
    padding-top: 1.56em;
    &__icon{
      width: 3.5em;
      margin-bottom: 1.25em;
    }
    &__title{
      .text36{
        font-size: 1.625em;
      }
    }
    &__items{
      gap: 1.875em;
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .result{
    padding-bottom: 6em;
    &__title{
      .text36{
        font-size: 1em;
        line-height: 150%;
        letter-spacing: -.03em;
      }
    }
    &__items{
      margin-top: 3.125em;
    }
  }

  .result-item{
    flex-direction: column;
    text-align: center;
    &__img{
      margin: 0 0 1.25em;
    }
  }
}
