.opinion{
  padding: 7.5em 0;
  &__inner{
    display: flex;
    align-items: flex-start;
  }
  .author__img{
    border-width: 4px;
    width: 18.125em;
    height: 18.125em;
    margin: 0 6.25em 0 0;
    flex-shrink: 0;
  }
  &__content{
    flex-grow: 1;
    border-radius: 1.25em;
    padding: 1.875em 2.44em;
    background-color: $gray;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 9.5em;
      height: 9.75em;
      @include bgImgSize(100%);
      background-image: url(../img/triangle3.svg);
      left: -4.88em;
      top: 10em;
    }
  }
  .text{
    position: relative;
    z-index: 2;
    p{
      @include lastM(0 0 .9em);
    }
  }
}


@media screen and (max-width: 1280px){
  .opinion{
    &__content{
      padding: 2.06em 2.25em;
    }
    .text{
      p{
        @include lastM(0 0 1em);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .opinion{
    .author__img{
      width: 15em;
      height: 15em;
      margin-right: 5.94em;
    }
    &__content{
      padding: 1.44em 1.56em;
      &:before{
        width: 8.4375em;
        height: 8.5625em;
        left: -3.31em;
        top: 7.31em;
      }
    }
    .text{
      p{
        @include lastM(0 0 1.25em);
      }
    }
  }
}


@media screen and (max-width: 768px){
  .opinion{
    padding: 6.25em 0;
    &__inner{
      flex-direction: column;
      align-items: center;
    }
    .author__img{
      margin: 0 auto 4em;
    }
    &__content{
      width: 100%;
      padding: 1.62em 1.94em;
      &:before{
        width: 4.3125em;
        height: 7em;
        left: 50%;
        top: -3em;
        transform: translateX(-50%);
        background-image: url(../img/triangle4.svg);
      }
    }
    .text{
      p{
        @include lastM(0 0 .94em);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .opinion{
    .author__img{
      border-width: 3px;
      margin-bottom: 4.25em;
      width: 12.5em;
      height: 12.5em;
    }
    &__content{
      padding: .81em .94em;
    }
  }
}
