.plan{
  padding: 7.5em 0;
  background-color: $gray;
  &__top{
    width: 57.25em;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
    @include grid100gap(2.875em);
  }
  &__items{
    margin: 3.75em auto 0;
    width: 56em;
    max-width: 100%;
    @include grid100gap(3.125em);
  }
}

.plan-item{
  position: relative;
  padding-left: 11em;
  min-height: 6.6125em;
  &__img{
    position: absolute;
    width: 8.3125em;
    left: 0;
    top: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    text-transform: uppercase;
  }
  &__text{
    margin-top: .625em;
    font-size: 1em;
    line-height: 160%;
    letter-spacing: -0.03em;
    p{
      line-height: inherit;
      @include lastM(0 0 .5em);
    }
  }
}


@media screen and (max-width: 1280px){
  .plan{
    &__top{
      width: 53em;
    }
    &__items{
      margin-top: 3.875em;
      width: 51.875em;
    }
  }
}


@media screen and (max-width: 992px){
  .plan{
    &__top{
      gap: 2.19em;
      width: 45.5em;
    }
    &__items{
      margin-top: 3.75em;
      width: 49.75em;
      gap: 2.5em;
    }
  }

  .plan-item{
    padding-left: 10.315em;
    &__img{
      width: 7.625em;
    }
  }
}


@media screen and (max-width: 768px){
  .plan{
    padding: 6.25em 0;
    &__top{
      width: 40.75em;
    }
    &__items{
      margin-top: 3.125em;
      width: 41.75em;
    }
  }

  .plan-item{
    padding-left: 9em;
  }
}


@media screen and (max-width: 576px){
  .plan{
    &__top{
      gap: 1.69em;
    }
    &__items{
      gap: 1.875em;
    }
  }

  .plan-item{
    padding: 0;
    &__top{
      display: flex;
      align-items: center;
    }
    &__img{
      position: static;
      width: 6.1875em;
      margin-right: .625em;
      flex-shrink: 0;
    }
    &__text{
      margin-top: .94em;
    }
  }
}
